import * as React from "react";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";
import InfoIcon from "@material-ui/icons/Info";
import Typography from "@material-ui/core/Typography";
import Modal from "@material-ui/core/Modal";
import { makeStyles } from "@material-ui/core/styles";
import { red } from "@material-ui/core/colors";
const useStyles = makeStyles((theme) => ({
  style: {
    position: "fixed",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: "50%",
    background: "#fff",
    border: "2px solid #000",
    boxShadow: 24,
    padding: 10,
    textAlign: "center",
    [theme.breakpoints.down("sm")]: {
      width: "70%",
    },
  },
  info: {
    position: "fixed",
    fontSize: "60px",
    zIndex: "99",
    color: red[500],
    top: "85%",
    right: "5%",
    background: "#000",
    borderRadius: "100%",
  },
}));

export default function ModalPemberitahuan() {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const classes = useStyles();
  return (
    <div>
      <InfoIcon onClick={handleOpen} className={classes.info} />
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box style={{ background: "#fff" }} className={classes.style}>
          <Typography id="modal-modal-title" variant="body1" component="h2">
            Dibuat : ReactJs,Material UI dan API
          </Typography>
          <Typography id="modal-modal-description" style={{ marginTop: 2 }}>
            Pembuat : Muhammad Fajri
          </Typography>
          <Typography variant="caption"> Sentuh dimana saja untuk menutup</Typography>
        </Box>
      </Modal>
    </div>
  );
}
