import {
  AppBar,
  Box,
  Button,
  Card,
  CardContent,
  CardMedia,
  CircularProgress,
  Grid,
  Paper,
  TextField,
  Toolbar,
  Typography,
} from "@material-ui/core";
import React, { useState, useEffect } from "react";
import mockData from "./mockData";
import axios from "axios";
import { makeStyles } from "@material-ui/core/styles";
import { Search } from "@material-ui/icons";
import { lightBlue } from "@material-ui/core/colors";
import ModalPemberitahuan from "./ModalPemberitahuan";
const useStyles = makeStyles((theme) => ({
  root: {
    fontFamily: "Poppins",
  },

  pokedexContainer: {
    padding: "20px 0 10px 10px",
    flexWrap: "wrap",
    width: "100%",
  },
  cardmedia: {
    margin: "auto",
  },
  namePokemon: {
    textTransform: "capitalize",
    fontWeight: "bold",
    textAlign: "center",
    [theme.breakpoints.down("sm")]: {
      fontSize: "13px",
    },
  },
  buttonPokemon: {
    width: "100%",
    fontWeight: "bold",
  },
  searchContainer: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    width: "100%",
    marginTop: "10px",
  },
  boxSearch: {
    display: "flex",
    justifyContent: "center",
    alignItems: "flex-end",
    background: "#fff",
    padding: "10px",
    borderRadius: "10px",
  },
}));

const Pokedex = (props) => {
  const { history } = props;
  const classes = useStyles();
  const [pokemonData, setPokemonData] = useState(undefined);
  const [filter, setFilter] = useState("");
  const handleSearchChange = (e) => {
    setFilter(e.target.value);
  };
  //Useeffect
  useEffect(() => {
    axios.get(`https://pokeapi.co/api/v2/pokemon?limit=50`).then(function (response) {
      const { data } = response;
      const { results } = data;
      const newPokemonData = {};
      results.forEach((pokemon, index) => {
        newPokemonData[index + 1] = {
          id: index + 1,
          name: pokemon.name,
          sprite: `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${
            index + 1
          }.png`,
        };
      });
      setPokemonData(newPokemonData);
    });
  }, []);
  //
  const getPokemonCard = (pokemonId) => {
    console.log(pokemonData[`${pokemonId}`]);
    const { id, name, sprite } = pokemonData[pokemonId];
    return (
      <>
        <Grid item xs={6} sm={4} key={pokemonId}>
          <Card>
            <CardMedia
              className={classes.cardmedia}
              image={sprite}
              style={{
                width: "130px",
                height: "130px",
              }}
            ></CardMedia>
            <CardContent>
              <Typography className={classes.namePokemon}>{`${id}. ${name}`}</Typography>
            </CardContent>
            <Button
              className={classes.buttonPokemon}
              variant="contained"
              size="medium"
              color="primary"
              onClick={() => history.push(`/${pokemonId}`)}
            >
              Click Me
            </Button>
          </Card>
        </Grid>
      </>
    );
  };
  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Toolbar>
          <Typography
            variant="h2"
            align="center"
            style={{
              margin: "auto",
            }}
          >
            POKEDEX
          </Typography>
        </Toolbar>
      </AppBar>
      <div className={classes.searchContainer}>
        <Box className={classes.boxSearch}>
          <Search style={{ color: "action.active", mr: 1, my: 0.5, paddingRight: 5 }} />
          <TextField
            onChange={handleSearchChange}
            label="Cari dengan huruf kecil"
            variant="standard"
          />
        </Box>
      </div>
      <ModalPemberitahuan />
      {pokemonData ? (
        <Grid container spacing={2} className={classes.pokedexContainer}>
          {Object.keys(pokemonData).map(
            (pokemonId) => pokemonData[pokemonId].name.includes(filter) && getPokemonCard(pokemonId)
          )}
        </Grid>
      ) : (
        <CircularProgress />
      )}
    </div>
  );
};

export default Pokedex;
