import {
  Typography,
  Paper,
  Box,
  Badge,
  IconButton,
  Button,
  Avatar,
  CircularProgress,
} from "@material-ui/core";
import axios from "axios";

import React, { useEffect, useState } from "react";
import mockData from "./mockData";
import { makeStyles } from "@material-ui/core/styles";
import { lightBlue, lightGreen } from "@material-ui/core/colors";
import { ArrowBack } from "@material-ui/icons";
const useStyles = makeStyles((theme) => ({
  namaPokemon: {
    textTransform: "capitalize",
    display: "flex",
    justifyContent: "space-evenly",
    alignItems: "center",
    [theme.breakpoints.down("sm")]: {
      fontSize: "20px",
    },
  },
  containerPoke: {
    display: "flex",
    textAlign: "center",
    width: "fit-content",
    background: "#f4eee8",
    padding: "15px",
    gap: "5px",
    margin: "auto",
  },
  container: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    height: "100vh",
    [theme.breakpoints.down("sm")]: {
      height: "90vh",
    },
  },
  imgKecil: {
    width: 40,
    [theme.breakpoints.down("sm")]: {
      width: "40px",
    },
  },
  imgBesar: {
    width: "300px",
    height: "300px",
    margin: "auto",
    [theme.breakpoints.down("sm")]: {
      width: "200px",
      height: "200px",
    },
  },
  kembali: {
    position: "absolute",
    left: "2%",
    top: "5%",
    [theme.breakpoints.down("sm")]: {
      position: "relative",
      width: "100%",
      height: "10vh",
      bottom: "10%",
      left: "0",
      fontSize: "25px",
    },
  },
  namaPokemonInfo: {
    textTransform: "capitalize",
  },
}));
const Pokemon = (props) => {
  const classes = useStyles();
  const { match, history } = props;
  const { params } = match;
  const { pokemonId } = params;
  const [pokemon, setPokemon] = useState(mockData[`${pokemonId}`]);

  useEffect(() => {
    axios
      .get(`https://pokeapi.co/api/v2/pokemon/${pokemonId}/`)
      .then(function (response) {
        const { data } = response;
        setPokemon(data);
      })
      .catch(function (error) {
        setPokemon(false);
      });
  }, [pokemonId]);

  const generatorPokemonJSX = () => {
    const { name, id, species, height, weight, types, sprites } = pokemon;
    const fullImageUrl = `https://pokeres.bastionbot.org/images/pokemon/${id}.png`;
    const { front_default } = sprites;
    return (
      <div className={classes.container}>
        <Box className={classes.containerPoke} flexDirection="column">
          <Typography variant="h3" className={classes.namaPokemon}>
            {`${id}. ${name} `} <img className={classes.imgKecil} src={fullImageUrl} />
          </Typography>
          <img className={classes.imgBesar} src={fullImageUrl} />
          <Typography variant="h5">Pokemon Info</Typography>
          <Typography>Nama : {species.name} </Typography>
          <Typography>Berat : {height} Kg</Typography>
          <Typography>Tinggi : {weight} cm</Typography>
          {types.map((typeInfo) => {
            const { type } = typeInfo;
            const { name } = type;
            return <Typography key={name} className={classes.namaPokemon}>{`${name}`}</Typography>;
          })}
        </Box>
      </div>
    );
  };
  return (
    <>
      {pokemon === undefined && <CircularProgress />}
      {pokemon !== undefined && pokemon && generatorPokemonJSX()}
      {pokemon === false && <Typography> Pokemon tidak di temukan</Typography>}
      {pokemon !== undefined && (
        <Button
          color="secondary"
          className={classes.kembali}
          variant="contained"
          onClick={() => history.push("/")}
        >
          <ArrowBack fontSize="large" /> Kembali
        </Button>
      )}
    </>
  );
};

export default Pokemon;
